    transition: all 0.3s ease-in-out;  
} */

.nav-menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav-item {
    margin-left: 5rem;
}

.nav-link{
    font-size: 1.6rem;
    font-weight: 400;
    color: #475569;
}

.nav-link:hover{
    color: rgb(43, 101, 226)
}

.nav-logo {
    font-size: 2.1rem;
    font-weight: 500;
    color: rgb(43, 101, 226);
    
}

.nav_logo_img{
    width: 100;
    height: 100;
    border-radius:50%;
}

@media only screen and (max-width: 768px) {
    .nav-menu {
        position: fixed;
        right: -100%;
        top: 5rem;
        flex-direction: column;
        background-color: #fff;
        width: 75%;
        border-radius: 10px;
        text-align: center;
        transition: 0.3s;
        box-shadow:
            0 10px 27px rgba(0, 0, 0, 0.05);
        z-index: 100;
    }

    .nav-menu.active {
        right: 0;
    }

    .navbar_start{
        display: block;
        text-align: center;
        padding:12px 12px;
        text-decoration: none;
    }

    .nav-link{
        font-size: 1.8rem;
        font-weight: 400;
        color: #475569;
    }

    .nav-item {
        margin: 2.5rem 0;
    }

    .nav_burger {
        display: block;
        cursor: pointer;
        float: right;
        font-size: 1.8em;
        width: 25px;
        height: 3px;
        transition: all 0.3s ease-in-out;
        color: #fff;
    }

    .nav_logo_img{
        width: 100;
        height: 100;
        border-radius:50%;
    }


}


